import { Component, OnInit,NgModule } from '@angular/core';
import {movies} from '../shared/movies';
import {MessageService} from './../message.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  all_movies=Object.keys(movies);
  featured = ["Batman v Superman: Dawn of Justice","Avatar","Guardians of the Galaxy","The Amazing Spider-Man 2","Man of Steel","Interstellar"]
  featured_data=[]
  constructor(private message:MessageService) {
    this.featured_data=this.featured.map(val=>{
        var details=[movies[val]]
        details[0][9]=val;
        return details[0]
    })
  }
  clicked(val){
    this.message.sendMessage(this.featured[val]);
  }
  loadmovie0(){
    const ele = document.getElementsByClassName('bvs');
    const ele1 = document.getElementsByClassName('fir');
    Array.from(ele1).forEach((val)=>{
      setTimeout(()=>{
        val.classList.add('popper');
      },400)
    })
      var i=0;
      Array.from(ele).forEach((val)=>{
        i+=1
        setTimeout(()=>{
          val.classList.add('pop');
        },400)
      })
  }
  ngOnInit(): void {
    this.loadmovie0();
  }

}
