import { Component, OnInit } from '@angular/core';
import {menubar_toggle_animation,list_stagger} from '../animations/app.animation';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  animations: [menubar_toggle_animation(),list_stagger()],
})
export class HeaderComponent implements OnInit {
  menu_bool = false;
  toggle_navbar() {
    this.menu_bool = !this.menu_bool;
    if(this.menu_bool){
      this.loadmenu0()
    }
    else{
      this.loadmenu01();
    }
  }
  loadmenu0(){
    const ele = document.getElementsByClassName('menu-stagger0');
      var i=0;
      Array.from(ele).forEach((val)=>{
        i+=1
        setTimeout(()=>{
          val.classList.add('menu-stagger-pos0');
        },400*i)
      })
  }
  search(){
    this.menu_bool=false;
    this.loadmenu01();
    location.href = "#";
    location.href = "#search-0";
  }
  loadmenu01(){
    const ele = document.getElementsByClassName('menu-stagger0');
      var i=0;
      Array.from(ele).forEach((val)=>{
        i+=1
        setTimeout(()=>{
          val.classList.remove('menu-stagger-pos0');
        },400*i)
      })
  }
  constructor() {
    this.loadmenu()
   }
  showFiller = false;
  loadmenu(){
    const ele1 = document.getElementsByClassName('headani');
    var i=0;
    Array.from(ele1).forEach((val)=>{
      i+=1
      setTimeout(()=>{
        val.classList.add('pop');
      },130*i)
    })
      const ele = document.getElementsByClassName('menu-stagger');
      var i=0;
      Array.from(ele).forEach((val)=>{
        i+=1
        setTimeout(()=>{
          val.classList.add('menu-stagger-pos');
        },300*i)
      })
  }
  ngOnInit(): void {
    this.loadmenu()
  }

}
